const app = getApp()
import Authorization from "../../utils/authorize.js"
import cookieUtil from "../../utils/cookie.js"
export default {
	data() {
		return {
			gridList: [{
				text: "我的收藏",
				src: "../../static/collection.png",
				status: false
			},
			{
				text: "我的工单",
				src: "../../static/workorder.png",
				status: false
			},
			{
				text: "我的约伴",
				src: "../../static/friends.png",
				status: false
			}],
			pageList: [{
				text: "身份认证",
				src: "/static/auth.png",
				openType: "",
			},
			{
				text: "关于我们",
				src: "/static/about.png",
				openType: ""
			},{
				text: "推荐给好友",
				src: "/static/share.png",
				openType: "share"
			}],
			userInfo: null,
			tip: "点击授权",
		}
	},
	onShow() {
		console.log("onshow")
	},
	onLoad() {
		console.log("onload")
		// this.getUserInfoBySetting()
		console.log(app.globalData.userInfo)
		if(app.globalData.userInfo){
			console.log(app.globalData.userInfo)
			this.userInfo = app.globalData.userInfo
		}
	},
	methods: {
		/*列表点击事件*/
		// 点击跳转或者授权
		listClick(items, index){
			this.navigato(index)
		},
		// 跳转页面
		navigato(index){
			if(index==0){
				this.loadingAndNavigato("../authentication/authentication")
			}
			if(index==1){
				this.loadingAndNavigato("../about/about")
			}
		},
		loadingAndNavigato(src){
			uni.showLoading({
				title: "跳转中",
				success() {
					uni.navigateTo({
						url: src
					})
				},
				complete() {
					uni.hideLoading()
				}
			})
		},
		/*九宫格点击事件*/
		// 按钮变色
		changeColor(bool, items, index){
			var that = this
			var gridList = that.gridList
			gridList[index].status = bool
			that.gridList = gridList
		},
		// 用户松开点击
		gridEndClick(items, index){
			// 按钮变回白色
			this.changeColor(false, items, index)
			// 页面跳转
			
		},
		// 用户点击态
		gridClick(items, index){
			// 按钮灰变色
			this.changeColor(true, items, index)
		},
		
		// 首先获取用户信息设置到globalData，然后携带信息请求服务端登陆接口并且将登录状态设置为缓存
		onGotUserInfo(e) {
			var that = this
		    console.log(e.detail.userInfo)
		    app.globalData.userInfo = e.detail.userInfo
			that.userInfo = app.globalData.userInfo
			that.setCookie(app)
		},
		// 获取code携带userInfo同步向服务端发起登录请求，并且将cookie设置为缓存
		setCookie: async function (app) {
			var result = await Authorization.login(app)
			console.log(result)
			// 如果登陆成功
			if(result.data.code==0){
				var cookie = cookieUtil.getSessionIDFromResponse(result)
				cookieUtil.setCookieToStorage(cookie)
			}else{
				this.userInfo = null
				uni.showModal({
					title: "登录错误",
					content: "登录失败，请重新点击登陆",
					showCancel: false,
					confirmText: '确定'
				})
			}
		}
	}
}
